<template>
  <section>

    <div class="slider3">
      <b-slider
        @dragend="updateRow2"
        v-model="value.row2"
        type="is-black"
        size="is-large"
        max=1023
      ></b-slider>
    </div>

  </section>
</template>

<script>
import { db } from '../db.js'

export default {
  data () {
    return {
      value: { row2: 20 }
    }
  },
  firebase: {
    value: db.ref("row2")
  },
  methods: {
    updateRow2: function () {
      db.ref('row2').update({ row2: this.value.row2 }).then(() => {
        console.log('row2 updated!')
      })
    }
  }
}
</script>

<style>
.slider3 {
  margin-top: 45vh;
  width: 95vw;
  position: absolute;
  margin-left: 2.5vh;
  margin-right: 2.5vh;
}
</style>