
<template>
  <section>
    <div>
      <h1 style="font-size: 50px;">8 Hyllan</h1>
      <b-switch
        class="bswitch"
        v-model="switchCustom.power"
        true-value=1
        false-value=0
        size="is-large"
        grouped
        position="is-right"
        @input="updatePower"
      >

      </b-switch>
    </div>

  </section>
</template>


<script>
import { db } from '../db.js'
export default {
  data () {
    return {

      switchCustom: 1
    }

  },

  firebase: {
    switchCustom: db.ref('power'),
  },
  methods: {
    updatePower: function () {
      db.ref("power").update({ power: this.switchCustom.power }).then(() => {
        console.log('power updated!')
      })
      db.ref("row0").update({ row0: this.switchCustom.power * 1023 }).then(() => {
        console.log('row0 updated!')
      })
      db.ref("row1").update({ row1: this.switchCustom.power * 1023 }).then(() => {
        console.log('row1 updated!')
      })
      db.ref("row2").update({ row2: this.switchCustom.power * 1023 }).then(() => {
        console.log('row2 updated!')
      })
      db.ref("row3").update({ row3: this.switchCustom.power * 1023 }).then(() => {
        console.log('row3 updated!')
      })
    }
  }

}
</script>

<style>
.bswitch {
  position: absolute;
  float: right;
}
</style>