<template>
  <section>

    <div class="slider2">
      <b-slider
        @dragend="updateRow1"
        v-model="value.row1"
        type="is-black"
        size="is-large"
        max=1023
      ></b-slider>
    </div>

  </section>
</template>

<script>
import { db } from '../db.js'

export default {
  data () {
    return {
      value: { row1: 20 }
    }
  },
  firebase: {
    value: db.ref("row1")
  },
  methods: {
    updateRow1: function () {
      db.ref('row1').update({ row1: this.value.row1 }).then(() => {
        console.log('row1 updated!')
      })
    }
  }
}
</script>

<style>
.slider2 {
  margin-top: 30vh;
  width: 95vw;
  position: absolute;
  margin-left: 2.5vh;
  margin-right: 2.5vh;
}
</style>