<template>
  <section>

    <div class="slider4">
      <b-slider
        @dragend="updateRow3"
        v-model="value.row3"
        type="is-black"
        size="is-large"
        max=1023
      ></b-slider>
    </div>

  </section>
</template>

<script>
import { db } from '../db.js'

export default {
  data () {
    return {
      value: { row3: 20 }
    }
  },
  firebase: {
    value: db.ref("row3")
  },
  methods: {
    updateRow3: function () {
      db.ref('row3').update({ row3: this.value.row3 }).then(() => {
        console.log('row3 updated!')
      })
    }
  }
}
</script>

<style>
.slider4 {
  margin-top: 60vh;
  width: 95vw;
  position: absolute;
  margin-left: 2.5vh;
  margin-right: 2.5vh;
}
</style>