<template>
  <section>

    <div class="slider1">
      <b-slider
        @dragend="updateRow0"
        v-model="value.row0"
        type="is-black"
        size="is-large"
        max=1023
      ></b-slider>
    </div>

  </section>
</template>

<script>
import { db } from '../db.js'

export default {
  data () {
    return {
      value: { row0: 20 }
    }
  },
  firebase: {
    value: db.ref("row0")
  },
  methods: {
    updateRow0: function () {
      db.ref('row0').update({ row0: this.value.row0 }).then(() => {
        console.log('row0 updated!')
      })
    }
  }
}
</script>

<style>
.slider1 {
  margin-top: 15vh;
  width: 95vw;
  position: absolute;
  margin-left: 2.5vh;
  margin-right: 2.5vh;
}
</style>